.card {
    background: #fff;
    border-radius: 2px;
    height: 400px;
    margin: 1rem;
    position: relative;
    width: 400px;
    -webkit-box-shadow: 0 3px 6px #999, 0 3px 6px #999;
            box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    overflow: hidden;
    display:-ms-flexbox;
    display:flex;
    -ms-flex-align:center;
        align-items:center;
    -ms-flex-pack: center;
        justify-content: center;
  }

  .card img {
      width: 50%;
      height:auto;
  }
  
  /* Slide */
   .hover img {
      -webkit-transform: scale(1.5);
      -ms-transform: scale(1.5);
          transform: scale(1.5);
      -webkit-transition: .3s ease-in-out;
      -o-transition: .3s ease-in-out;
      transition: .3s ease-in-out;
  }


